<template>
	<div id="singleBlog">
		<h1>{{blog.title}}</h1>
		<article>
			{{blog.body}}
		</article>
		<p>博客分类</p>
	      <ul>
	        <li v-for="category in blog.categories">
	          {{category}}
	        </li>
	      </ul>
		<p>作者:{{blog.author}}</p>
	</div>
</template>
<script>
	export default{
		name:"singleBlog",
		data(){
			return {
				id: this.$route.params.id,
				blog:{}
			}
		},
		created(){
			var url = "https://wd4541854493vxxnak.wilddogio.com/posts/";
			this.$http.get(url+this.id+".json").then(function(data){
				return data.json()
				//this.blog = data.body;
				
			}).then(function(data){
				this.blog = data;
				console.log(data)
			})
		}
	}
</script>
<style>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}	
	article{
		word-wrap:break-word;
	}
</style>


